function getProduct(key) {
  const products = productJson;
  const filtered = products.filter((item) => item.key===key);
  return filtered[0];
}

const TransmissionList = (props) => {
  const { sizeList, setActiveSize, activeSize } = props;
  return (
      <div className={styles.root}>
        <div className={styles.sizeLabelContainer}>
          <span className={styles.label}>Transmission</span>
        </div>
        <div className={styles.sizeSelection}>
          {sizeList?.map((sizeOption, index) => {
            return (
                <BoxOption
                    key={index}
                    data={sizeOption}
                    setActive={setActiveSize}
                    isActive={activeSize === sizeOption}
                />
            );
          })}
        </div>
      </div>
  );
};

